class DetailComponent {
    constructor(activatedRoute, authService, pharmacyService, notificationService, notifyService, router) {
        this.activatedRoute = activatedRoute;
        this.authService = authService;
        this.pharmacyService = pharmacyService;
        this.notificationService = notificationService;
        this.notifyService = notifyService;
        this.router = router;
        this.result = null;
    }
    ngOnInit() {
        this.activatedRoute.params.subscribe(params => {
            this.prescriptionID = params['id'];
            if (this.prescriptionID) {
                this.get();
            }
        });
    }
    get() {
        this.pharmacyService.getPrescription(this.prescriptionID).subscribe((res) => {
            if (+res.status === 200) {
                this.result = res.data.docs[0];
                console.log(this.result);
            }
        });
    }
    updatePrescriptionStatus() {
        this.pharmacyService.updatePrescriptionStatus(this.prescriptionID).subscribe((res) => {
            if (+res.status === 200) {
                this.notifyService.success("Marked Successfully");
                this.sendNotification();
                this.result.isPrescriptionGiven = true;
            }
            else {
            }
        });
    }
    sendNotification() {
        let notObj = {
            createdBy: this.authService.currentUserValue._id,
            description: 'Prescription is Provided ',
            category: 5,
            sentTo: [this.result.patient._id, this.result.createdBy._id],
        };
        this.notificationService.sendNotification(notObj);
    }
}


