class ListComponent {
    constructor(router, pharmacyService) {
        this.router = router;
        this.pharmacyService = pharmacyService;
        this.result = null;
        this.page = 1;
        this.limit = 10;
    }
    ngOnInit() {
        this.getAllPrescriptions();
    }
    getAllPrescriptions() {
        this.pharmacyService.getPrescriptions(this.page, this.limit).subscribe((res) => {
            this.result = res;
            console.log(this.result);
        });
    }
    onClick(id) {
        this.router.navigate(['app/prescription/', id]);
    }
    pageChange(page) {
        this.page = page;
        this.getAllPrescriptions();
    }
}


